<template>
    <div class="container">
      <div class="header">
        <div class="header__icons">
          <img src="../assets/The Weather Channel.svg" class="header__icons__icon1">
          <div class="header__icons__divider"></div>
          <img src="../assets/timesOfIndia.png" class="header__icons__icon2">
        </div>
        <div class="header__searchbar"> 
          <input type="text" class="header__searchbar__input" placeholder="Search City or Postcode">
          <img src="../assets/Search.svg" class="header__searchbar__icon">
        </div>
        <div class="header__selector">
          <button class="selector__city" @click="shouldShowCitySelector = !shouldShowCitySelector">
            <img src="../assets/Globe.svg" class="selector__city__globe">
            <span class="selector__city__text">
              {{ headerContent.country }}
            </span>
            <span class="selector__city__text selector__city__text--border-left">
              {{ temparatureValue }}
            </span>
            <img src="../assets/menu-up.svg" class="selector__city__arrow" v-if="shouldShowCitySelector">
            <img src="../assets/Arrow down-1.svg" class="selector__city__arrow" v-else>
          </button>
          <button class="selector__hamburger" @click="shouldShowHamburgerSelector = !shouldShowHamburgerSelector">
            <span 
            class="selector__hamburger__menu "
            :class="shouldShowHamburgerSelector ? 'selector__hamburger__menu--selected' : ''"
            >
          </span>
          </button>
        </div>
        <city-selector-dropdown 
        v-if="shouldShowCitySelector"
        class="header__dropdown" 
        :selectedTemparatures="temparatureSelected" 
        @updateTemparature="updateTemparatureValue" 
        />
        <hamburgur-dropdown-vue
        v-if="shouldShowHamburgerSelector"
        class="header__dropdown"
        />
      </div>
      <display-city />
      <time-schedule />
      
  </div>
</template>

<script lang="ts" setup>
import {ref} from 'vue'

import headerContent from '../headerContent.json'
import { TemparatureSelected } from '@/types';
import CitySelectorDropdown from './CitySelectorDropdown.vue';
import HamburgurDropdownVue from './HamburgerDropdown.vue';
import DisplayCity from './DisplayCity.vue'
import TimeSchedule from './TimeSchedule.vue'

const shouldShowCitySelector = ref<boolean>(false)
const shouldShowHamburgerSelector = ref<boolean>(false)
const temparatureValue = ref<string>('°C')
const temparatureSelected = ref<TemparatureSelected>({
    fahrenheit: false,
    celcius: true,
    hybrid: false,
})

const updateTemparatureValue = (temparature:string) => {
  temparatureValue.value = temparature  

  for(let temp in temparatureSelected.value) {
      temparatureSelected.value[temp] = false
  }

  if(temparature === headerContent.temparatures.celcius) {
      temparatureSelected.value.celcius = true
  } else if(temparature === headerContent.temparatures.fahrenheit) {
      temparatureSelected.value.fahrenheit = true
  } else {
      temparatureSelected.value.hybrid = true
  }
}
</script>
  
