<template>
    <div class="cityselector">
        <div class="cityselector__header">
            <ul 
            class="header__list"
            >
                <li 
                class="header__list__item"
                :class="props.selectedTemparatures.fahrenheit ? 'header__list__item--selected' : ''"
                @click="handleTemparatureClick(headerContent.temparatures.fahrenheit)"
                >
                    {{ headerContent.temparatures.fahrenheit }}
                </li>
                <li 
                class="header__list__item"
                :class="props.selectedTemparatures.celcius ? 'header__list__item--selected' : ''"
                @click="handleTemparatureClick(headerContent.temparatures.celcius)"
                >
                    {{ headerContent.temparatures.celcius }}
                </li>
                <li 
                class="header__list__item"
                :class="props.selectedTemparatures.hybrid ? 'header__list__item--selected' : ''"
                @click="handleTemparatureClick(headerContent.temparatures.hybrid)"
                >
                    {{headerContent.temparatures.hybrid}}
                </li>
            </ul>
            <span class="header__text">{{ temparatureMeasures }}</span>
        </div>
        <div class="cityselector__dropdown">
            <dropdown-item v-for="city in Object.keys(headerContent.cityNames)" 
            :key="city"            
            :cityName="city"
            :shouldShowCityList="showCityList[city]"
            @handleCityClick="handleCityClickk(city)"
            />
        </div>
    </div>
</template>

<script setup lang="ts">
import {ref, defineEmits, defineProps, computed} from 'vue'

import {CityList} from '../types'
import headerContent from '../headerContent.json'
import DropdownItem from './CitySelector/DropdownItem.vue'


const emit = defineEmits(['updateTemparature'])
const props = defineProps(['selectedTemparatures'])
const shouldShowCityList = ref<boolean>(true)
const showCityList = ref<CityList>({
    AMERICA: false,
    AFRICA: false,
    'ASIA PACIFIC': false,
    EUROPE: false,
    'MIDDLE EAST' : false,
})

const temparatureMeasures = computed(() => {
    if(props.selectedTemparatures.fahrenheit) {
        return 'Imperial - F / mph / miles / inches'
     } else if(props.selectedTemparatures.celcius) {
        return 'Meter-C / millimeters / km / millibars'
     } else {
         return 'Hybrid - C / millimeters / miles / mph / millibars'
     }
})

const handleCityClickk = (cityName:string) => {
    for(let city in showCityList.value) {        
        if(cityName !== city) showCityList.value[city] = false;
    }
    showCityList.value[cityName] = !showCityList.value[cityName]
}

const handleTemparatureClick = (temparature:string) => {
    emit('updateTemparature', temparature)
}
</script>

